/**
 * Creates DOM element to be used as React root.
 */
function createRootElement(id) {
  var rootContainer = document.createElement('div');
  rootContainer.setAttribute('id', id);
  return rootContainer;
}
/**
 * Appends element as last child of body.
 */


function addRootElement(rootElem) {
  document.body.appendChild(rootElem);
}
/**
 * Creates or return the current container DOM element to be the portal root.
 */


var portalContainer = function portalContainer(id) {
  var rootElement = createRootElement(id);
  var parent = document.querySelector("#".concat(id));

  if (!parent) {
    addRootElement(rootElement);
  }

  return rootElement;
};

export default portalContainer;